import React, { useState } from 'react';
import NavTop from '../components/NavTop'
import { Axios, db } from '../firebase/firebaseConfig';

const Contact = () => {
  const [formData, setFormData] = useState({})
  const [loading, setLoading] = useState(false);

  const updateInput = e => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    })
  }

  const handleSubmit = (event) => {
    event.preventDefault()

    setLoading(true);
    Axios.post(
      'https://us-central1-antony999kwebsite.cloudfunctions.net/submit',
      formData,
      {
        headers: {
          'Content-type': 'application/json; charset=utf-8',
            'Access-Control-Allow-Origin':'*',
            'Access-Control-Allow-Methods': 'POST',
            'Access-Control-Allow-Headers':'Content-Type',
            'cache-control': 'no-cache'
        }
      }
    )
      .then(res => {
        db.collection('emails').add({
          name: formData.name,
          email: formData.email,
          message: formData.message,
          time: new Date(),
        })
        setFormData({
          name: '',
          email: '',
          message: '',
        })
        alert("Message succesfully sended.");
        setLoading(false);
      })
      .catch(error => {
        alert(error);
        //console.log(error);
        setFormData({
          name: '',
          email: '',
          message: '',
        })
        document.getElementById("contactForm").reset();
        setLoading(false);
      })
  }

  return (
    <div id="contact">
      <NavTop></NavTop>

      <div className="content">
        <div className="info">
          <h1 className="title">Get in touch</h1>
          <p>Feel free to get in touchwith me. I'm always open to discussing new projects, creative ideas or opportunities to be part of your visions.</p>

          <div className="help">
            <span>Need help? </span>
            <a href="mailto:adrian.moraar@gmail.com">adrian.moraar@gmail.com</a>
          </div>

          <br />

          <div className="links">
            <span>Feel like talking.</span>
            <div className="socialNetworks"></div>
          </div>
        </div>

        <div className="formContainer">
          <form onSubmit={handleSubmit} id="contactForm">
            <input type="text" placeholder="Name" className="input" onChange={updateInput}
              value={formData.name || ''} name="name" required />
            <input type="email" placeholder="E-mail" className="input" onChange={updateInput}
              value={formData.email || ''} name="email" required />
            <textarea placeholder="Project Description" className="input" onChange={updateInput}
              value={formData.message || ''} name="message" required></textarea>
            <button className="btn" type="submit" disabled={loading}>{!loading ? "Send Over" : "Loading"}</button>
          </form>
        </div>
      </div>
    </div>
  );
}

export default Contact;