import React from 'react';
import NavTop from '../components/NavTop'
import { Link } from 'react-router-dom';
import {ProjectService} from '../providers/ProjectService'


class Projects extends React.Component {
  render() {
    const service = new ProjectService();
    const projects = service.projects;

    return (
      <div className="projectsPage">
        <NavTop></NavTop>
        <div className="content">
        <h1 className="title">Projects</h1>

        <div className="projects">
          {projects.map((project) =>{
            return <div className="project"  key={project.key}>
            {!project.url &&             <Link to={{
              pathname:'/projects/' + project.key,
              state:{
                fromNotifications: true
              }
            }}>
              <div className="image"><img src={project.imgUrl} alt={project.title}></img></div>
            </Link>
            }
            {project.url && <a href={project.url} target="_blank" rel="noopener noreferrer"><div className="image"><img src={project.imgUrl} alt={project.title}></img></div></a>}
            <h4 className="title">{project.title}</h4>
            <span className="type">{project.type}</span>
          </div>
          })}
        </div>

        </div>
      </div>
  );
  }
}


export default Projects;