import React from 'react';
import NavTop from '../components/NavTop'
import { ProjectService } from '../providers/ProjectService';
import YouTube from 'react-youtube';
import "./DefaultProject.scss";
import githubImg from "../assets/images/viewOnGithub.png";

class DefaultProject extends React.Component {
  state = {
    project: ""
  };

  componentDidMount() {
    const projectID = this.props.match.params.id;
    const service = new ProjectService();
    this.setState({
      project: service.projects[projectID]
    });
  }

  render() {
    const opts = {
      height: '400',
      width: '80%',
      playerVars: {
        autoplay: 1,
      },
    }

    return (
      <div id="defaultProject">
        <NavTop></NavTop>
        <div className="headerImg">
          <img src={this.state.project.imgHeaderUrl} alt="Perzonalized Header"></img>
        </div>

        <div className="content">
          <div className="info">
            <div className="infoL">
              <h1 className="title">{this.state.project.title}</h1>
              <h2 className="type">{this.state.project.type}</h2>
              <span className="date">{this.state.project.date}</span>
            </div>
            <div className="infoR">

              {this.state.project.desc && this.state.project.desc.map((value, index) => {
                return (
                  <div>
                    <p key={index} className="desc">{value}</p>
                    {(index == 0 && this.state.project.githubLink) && <a target="_blank" href={this.state.project.githubLink}><img src={githubImg} className="githubImage" /></a>}
                  </div>
                )
              })}

              {(this.state.project.youtubeID || this.state.project.gifUrl) &&
                <div className="mainMediaContainer">
                  {this.state.project.youtubeID ?
                    <YouTube className="video" videoId={this.state.project.youtubeID} opts={opts} /> :
                    <img src={this.state.project.gifUrl} className="gif"></img>
                  }

                </div>
              }
            </div>
          </div>

          <div className="carrousel">
          </div>
        </div>


      </div>
    );
  }
}

export default DefaultProject;